import React, { Fragment } from "react"
//import axios from "axios"
import { Spinner } from "./Spinner"

const SearchKey = props => {
  const { checkDuplicateID, mcTable, setDisplayAlert, inspectionID, setInspectionID, clearScreen, searchInspByMC, currentMF, currentSU, editMode } = props

  const handleKeyChange = event => {
    const id = event.target.id
    const { value } = event.target

    switch (id) {
      case "searchKey_mc":
        setInspectionID({ ...inspectionID, mc: value })
        if (value.length >= 3) searchInspByMC(value)
        // pass first few characters of MC entered
        else clearScreen()
        break
      case "searchKey_iteration":
        setInspectionID({ ...inspectionID, iteration: value })
        break
      case "searchKey_type":
        setInspectionID({ ...inspectionID, type: value })
        break
      default:
        break
    }
  }

  const validateMC = id => {
    clearScreen()

    const found = mcTable.find(x => x.mc_no === id.mc)
    const sameMF = mcTable.find(x => x.mc_no === id.mc && x.su_no === currentSU && x.mf_no === currentMF)

    if (!found) {
      displayAlert("This MC doesn't exist")
    } else if (!sameMF) {
      displayAlert("This MC exists but not for this manufacturer!")
    } else {
      checkDuplicateID(id)
    }
  }

  function isInvalidID() {
    if (inspectionID.mc === "" || inspectionID.iteration === "Select" || inspectionID.iteration === "" || inspectionID.type === "Select") return true
    else return false
  }

  //encapsulate the props function as only message is different in this page
  const displayAlert = message => {
    setDisplayAlert({ message: message, position: 0, timeout: 3000, color: "alert__red" })
  }

  return (
    <Fragment>
      <div className="searchKey">
        <div className="searchKey_pair">
          <label>MC# : </label>
          <input type="text" name="searchKey_mc" id="searchKey_mc" onChange={handleKeyChange} />
        </div>
        <div className="searchKey_pair">
          <label>Iteration : </label>
          <input type="number" name="searchKey_iteration" id="searchKey_iteration" placeholder="Iteration" min="1" max="9" onChange={handleKeyChange} />
        </div>
        <div className="searchKey_pair">
          <label>Type : </label>
          <select name="searchKey_type" id="searchKey_type" onChange={handleKeyChange}>
            <option value="Select">Select</option>
            <option value="1st Line">1st In-line</option>
            <option value="2nd Line">2nd In-Line</option>
            <option value="3rd Line">3rd In-Line</option>
            <option value="4th Line">4th In-Line</option>
            <option value="5th Line">5th In-Line</option>
            <option value="1st Pre-final">1st Pre-final</option>
            <option value="2nd Pre-final">2nd Pre-final</option>
            <option value="Pre-Production Meeting">Pre-Production Meeting</option>
            <option value="Final">Final</option>
            <option value="Re-inspection for Failure">Re-inspection for Failure</option>
          </select>
        </div>
        <div></div>
        {!editMode ? (
          <div>
            {!isInvalidID() ? (
              <div>
                <button
                  onClick={() => {
                    validateMC(inspectionID)
                  }}
                >
                  Create Inspection
                </button>
              </div>
            ) : null}
          </div>
        ) : null}
      </div>
      <Spinner area="searchKey" />
    </Fragment>
  )
}

export default SearchKey
