import React, { Fragment } from "react"
import { Spinner } from "./Spinner"

const SearchedMC = props => {
  const { setInspectionID, handleSearch, searchedMC, setShowSearchedMC, mcTable, currentSU, currentMF } = props

  const newSearchedMC = []
  for (var i = 0; i < searchedMC.length; i++) {
    const mc_no = searchedMC[i].mc
    const rec = mcTable.find(x => x.mc_no === mc_no && x.su_no === currentSU && x.mf_no === currentMF)
    if (rec) {
      newSearchedMC.push(searchedMC[i])
    }
  }

  const handleClick = id => {
    setInspectionID(id)
    handleSearch(id)
    // Set search fields in SearchKey.js
    document.getElementById("searchKey_mc").value = id.mc
    document.getElementById("searchKey_iteration").value = id.iteration
    document.getElementById("searchKey_type").value = id.type
    setShowSearchedMC(false)
  }

  return (
    // present the table
    <Fragment>
      <div className="searchedMC">
        <div className="searchedMC_header1 searchedMC_header">
          <div></div>
          <div>MC</div>
          <div>Iteration</div>
          <div>Type</div>
        </div>
        <div className="searchedMC_header2 searchedMC_header">
          <div></div>
          <div>MC</div>
          <div>Iteration</div>
          <div>Type</div>
        </div>
        <div className="searchedMC_header3 searchedMC_header">
          <div></div>
          <div>MC</div>
          <div>Iteration</div>
          <div>Type</div>
        </div>
        {newSearchedMC.length > 0
          ? newSearchedMC.map(x => (
              <div
                className="searchedMC_detail"
                key={x.id}
                onClick={() => {
                  handleClick({ mc: x.mc, iteration: x.iteration, type: x.type })
                }}
              >
                <div></div>
                <div>{x.mc}</div>
                <div>{x.iteration}</div>
                <div>{x.type}</div>
              </div>
            ))
          : null}
      </div>
      <Spinner area="mfrSelection" />
    </Fragment>
  )
}

export default SearchedMC
