import { React, useState, useEffect } from "react"

const ItemTable = props => {
  useEffect(() => {
    const listener = event => {
      if (event.code === "Enter" || event.code === "NumpadEnter") {
        //console.log("Enter key was pressed. Run your function.");
        event.preventDefault()
        document.getElementById("itemTable_MCBtn").click()
      }
    }

    if (document.getElementById("itemTable_MC")) document.getElementById("itemTable_MC").addEventListener("keydown", listener)

    return () => {
      if (document.getElementById("itemTable_MC")) document.getElementById("itemTable_MC").removeEventListener("keydown", listener)
    }
  }, [])

  const { setItems, items, setDisplayAlert, mcTable, addMC, editMode, currentSU, currentMF } = props

  const sortTable = (sort, order) => {
    const newItems = [...items]
    /*table = document.getElementById("itemTable") */
    if (sort === 0 && order === "asc") {
      newItems.sort(function (a, b) {
        return a.mc_no > b.mc_no ? 1 : -1
      })
    } else if (sort === 0 && order === "desc") {
      newItems.sort(function (a, b) {
        return a.mc_no < b.mc_no ? 1 : -1
      })
    } else if (sort === 1 && order === "asc") {
      newItems.sort(function (a, b) {
        return a.item_no > b.item_no ? 1 : -1
      })
    } else if (sort === 1 && order === "desc") {
      newItems.sort(function (a, b) {
        return a.item_no < b.item_no ? 1 : -1
      })
    }
    setItems(newItems)
  }

  //* add new MC

  const [mc, setMC] = useState("")

  const handleInputChange = event => {
    const value = event.target.value
    setMC(value)
  }

  const validateMC = () => {
    //event.preventDefault()

    const found = mcTable.find(x => x.mc_no === mc)
    const duplicate = items.find(x => x.mc_no === mc)
    const sameMF = mcTable.find(x => x.mc_no === mc && x.su_no === currentSU && x.mf_no === currentMF)

    if (!found) {
      displayAlert("This MC doesn't exist")
    } else if (!sameMF) {
      displayAlert("This MC exists but not for this manufacturer!")
    } else if (duplicate === undefined) {
      addMC(mc)
    } else {
      displayAlert("MC was already entered in the item list, please check ! ")
    }
  }

  //encapsulate the props function as only message is different in this page
  const displayAlert = message => {
    setDisplayAlert({ message: message, position: 1, timeout: 1500, color: "alert__red" })
  }

  return (
    <div>
      <div className="itemTable_Section">
        <div>Add MC:</div>
        &nbsp;
        <div>{editMode ? <input type="text" id="itemTable_MC" name="mc" justify="left" placeholder="Additional MC here" onChange={handleInputChange} /> : <input type="text" id="itemTable_MC" name="mc" justify="left" placeholder="Include additional MC here" readOnly onChange={handleInputChange} />}</div>
        &nbsp;&nbsp;
        <div>
          {editMode ? (
            <i
              id="itemTable_MCBtn"
              className="fa fa-plus clickable"
              onClick={() => {
                validateMC(mc)
              }}
            ></i>
          ) : null}
        </div>
      </div>
      <div className="itemTable_Header">
        <div>
          By MC#&nbsp;
          <i
            className="fa fa-chevron-circle-up"
            onClick={() => {
              sortTable(0, "asc")
            }}
          ></i>
          &nbsp;
          <i
            className="fa fa-chevron-circle-down"
            onClick={() => {
              sortTable(0, "desc")
            }}
          ></i>
        </div>
        <div>
          Item&nbsp;
          <i
            className="fa fa-chevron-circle-up"
            onClick={() => {
              sortTable(1, "asc")
            }}
          ></i>
          &nbsp;
          <i
            className="fa fa-chevron-circle-down"
            onClick={() => {
              sortTable(1, "desc")
            }}
          ></i>
        </div>
        <div align="center">Order</div>
        <div></div>
      </div>

      <div id="itemTableScroller" className="itemTable_GridContainer">
        {items.length > 0
          ? items.map(item => (
              <div key={item.id} className="itemTable_Detail">
                <div>{item.mc_no}</div>
                <div>{item.item_no}</div>
                <div align="center">{item.order_qty}</div>
              </div>
            ))
          : null}
      </div>
    </div>
  )
}

export default ItemTable
