import React, { Fragment } from "react"

const MfrSelection = props => {
  const { partyTable, setCurrentSU, setCurrentMF, setCurrentSUName, setCurrentMFName, selectingSupplier, setSelectingSupplier } = props

  const handleClick = (su, mf, su_name, mf_name) => {
    setCurrentSU(su)
    setCurrentMF(mf)
    setCurrentSUName(su_name)
    setCurrentMFName(mf_name)
    setSelectingSupplier(false)
  }

  return (
    // present the table
    <Fragment>
      {partyTable && selectingSupplier ? <div className="mfrSelection_header">Select a manufacturer:</div> : null}
      {partyTable && selectingSupplier
        ? partyTable.map(x => (
            <div
              className="mfrSelection"
              key={x.SU.concat(x.MF)}
              onClick={() => {
                handleClick(x.SU, x.MF, x.SU_NAME, x.MF_NAME)
              }}
            >
              <div className="mfrSelectionSU">
                {x.SU_NAME} ({x.SU}){" "}
              </div>
              <div className="mfrSelectionMF">
                {x.MF_NAME} ({x.MF}){" "}
              </div>
            </div>
          ))
        : null}

      
    </Fragment>
  )
}
export default MfrSelection
