import { React, useEffect } from "react"

const ItemTotal = props => {
  const { setItemSearchString, itemsTotal, editMode, shippedTotal, sampleTotal, getSampleSize, aqlTable, setItemsTotal, setShippedTotal, setSampleTotal, inspectTotal, setInspectTotal, setAqlSampleTotal, aqlSampleTotal, undoTable, resetInspectQty, setDisplayAlert } = props

  useEffect(() => {
    const listener = event => {
      if (event.code === "Enter" || event.code === "NumpadEnter") {
        //console.log("Enter key was pressed. Run your function.");
        event.preventDefault()
        document.getElementById("highLightItemBtn").click()
      }
    }
    var el = document.getElementById('itemTotal_searchString');
    
    if(el){      
      el.addEventListener("keydown", listener)
      return () => {
        el.removeEventListener("keydown", listener)
       }
      }   

  }, [])

  useEffect(() => {
    const _total = [...itemsTotal]
    if (_total.length === 0) {
      setShippedTotal(0)
      setSampleTotal(0)
    } else {
      for (var i = 0; i < _total.length; i++) {
        const reducer1 = (accumulator, currentValue) => accumulator + parseInt(currentValue.ship_qty)
        const _shippedTotal = _total.reduce(reducer1, 0)
        setShippedTotal(_shippedTotal)

        // calculate total number of samples
        const reducer2 = (accumulator, currentValue) => accumulator + getSampleSize(aqlTable, currentValue.ship_qty, _shippedTotal)
        const _sampleTotal = _total.reduce(reducer2, 0)
        setSampleTotal(_sampleTotal)

        // calculate total number of inspection qty
        const reducer3 = (accumulator, currentValue) => accumulator + parseInt(currentValue.inspect_qty)
        const _inspectTotal = _total.reduce(reducer3, 0)
        setInspectTotal(_inspectTotal)

        // calculate AQL sample size
        setAqlSampleTotal(getSampleSize(aqlTable, shippedTotal, _shippedTotal))
      }
    }
  }, [itemsTotal, shippedTotal, sampleTotal, aqlTable, setSampleTotal, setShippedTotal, getSampleSize, inspectTotal, setInspectTotal, setAqlSampleTotal])

  useEffect(() => {
    const _total = [...itemsTotal]
    for (var i = 0; i < _total.length; i++) {
      const _sample_qty = getSampleSize(aqlTable, _total[i].ship_qty, shippedTotal)
      _total[i].sample_qty = _sample_qty
      //_total[i].inspect_qty = _sample_qty
    }
    setItemsTotal(_total)

    // eslint-disable-next-line
  }, [shippedTotal])

  const handleShipQty = item_no => event => {
    let value = event.target.value
    if (value === "") {
      return
    }
    if (value > 20000) {
      value = 20000
      setDisplayAlert({ message: "Number of ship units is too large!", position: 1, timeout: 3000, color: "alert__red" })
    }

    const newItemsTotal = [...itemsTotal]
    const index = newItemsTotal.findIndex(r => r.item_no === item_no)
    const newEntry = newItemsTotal.find(r => r.item_no === item_no)
    newItemsTotal[index] = { ...newEntry, ship_qty: value }
    setItemsTotal(newItemsTotal)
  }

  const handleInspectQty = item_no => event => {
    let value = event.target.value
    const newItemsTotal = [...itemsTotal]
    const index = newItemsTotal.findIndex(r => r.item_no === item_no)
    const newEntry = newItemsTotal.find(r => r.item_no === item_no)
    newItemsTotal[index] = { ...newEntry, inspect_qty: value }
    setItemsTotal(newItemsTotal)
  }

  return (
    // present the table

    <div>
      <div className="itemTotal_Section">
        <div>Highlight item :</div>

        <div>
          <input type="text" name="itemTotal_searchString" id="itemTotal_searchString" justify="left" onInput={e => (e.target.value = ("" + e.target.value).toUpperCase())} placeholder="Enter item here" />
        </div>

        <div>
          <i
            className="fa fa-search"
            id="highLightItemBtn"
            onClick={() => {
              setItemSearchString(document.getElementById("itemTotal_searchString").value)
            }}
          ></i>
        </div>

        <div>
          Refresh Qty.&nbsp;
          <i
            className="fa fa-angle-double-right itemTotal_arrow_color"
            onClick={() => {
              resetInspectQty()
            }}
          ></i>
          <i
            className="fa fa-angle-double-right  itemTotal_arrow_color"
            onClick={() => {
              resetInspectQty()
            }}
          ></i>
        </div>

        <div>
          Undo
          <i
            className="fa fa-undo"
            onClick={() => {
              undoTable()
            }}
          ></i>
        </div>
      </div>

      <div className="itemTotal_Header">
        <div>By Item</div>
        <div>Ship</div>
        <div>Sample</div>
        <div>Inspect</div>
        <div>Delete</div>
      </div>

      <div id="scroller" className="itemTotal_GridContainer">
        {itemsTotal
          ? itemsTotal.map(item => (
              <div key={item.item_no} className="itemTotal_Detail">
                <div>{item.item_no}</div>
                <div>
                  <input type="text" id="itemTotal_shipQty" name="itemTotal_shipQty" value={item.ship_qty || ""} justify="left" onChange={handleShipQty(item.item_no)} readOnly={editMode ? "" : "readOnly"} />
                </div>
                <div>{getSampleSize(aqlTable, item.ship_qty, shippedTotal)}</div>
                <div>
                  <input type="text" id="itemTotal_InspectQty" name="itemTotal_InspectQty" value={item.inspect_qty || ""} justify="left" placeholder="" onChange={handleInspectQty(item.item_no)} readOnly={editMode ? "" : "readOnly"} />
                </div>
                <div>
                  {editMode ? (
                    <i
                      className="fa fa-times clickable"
                      onClick={() => {
                        props.deleteAllItems(item.item_no)
                      }}
                    ></i>
                  ) : null}
                </div>
              </div>
            ))
          : null}
      </div>

      <div className="itemTotal_Total">
        <div> Total :</div>
        <div>{shippedTotal}</div>
        <div>{aqlSampleTotal}</div>
        <div className={aqlSampleTotal !== inspectTotal ? "highlightSection" : null}>{inspectTotal}</div>
        <div></div>
      </div>
    </div>
  )
}

export default ItemTotal
